.valueItem .arrow {
    width: 230px;
    height: 50px;
    transform: rotate(-45deg);
    background: rgb(38, 179, 255);
    background: linear-gradient(-90deg, rgba(38, 179, 255, 1) 30%, rgba(218, 251, 255, 1) 100%);
}

.valueItem:nth-of-type(even) .arrow {
    background: rgb(57, 172, 98);
    background: linear-gradient(-90deg, rgba(57, 172, 98, 1) 30%, rgba(237, 244, 242, 1) 100%);
}

.arrow::after,
.arrow::before {
    width: 180px;
    height: 50px;
}

.arrow::after {
    top: -46px;
    right: -70px;
    background: rgb(38, 179, 255);
}


.arrow::before {
    background: rgb(38, 179, 255);
    top: 46px;
    right: -70px;
}

.valueItem:nth-of-type(even) .arrow:before,
.valueItem:nth-of-type(even) .arrow:after {
    background: rgb(57, 172, 98);
}

.arrowContainer {
    position: relative;
    height: 235px;
    width: 235px;
}

.arrow {
    position: absolute;
    top: 100px;
    right: 10px;
}

.valueNr {
    font-weight: 900;
    color: transparent;
    -webkit-text-stroke: 1.5px var(--white);
    stroke-width: 1.5px;
    position: absolute;
    right: 20px;
    top: 15px;
    font-size: 30px;
    letter-spacing: 2px;
    font-family: 'Kanit';
}

.values {
    display: flex;
    justify-content: center;
}

.valueItem {
    width: 380px
}

.valueItem h3 {
    font-size: 30px;
    color: var(--blue);
    margin-top: -30px;
    margin-bottom: 10px;
    margin-left: 80px;
}

.valueItem:nth-of-type(even) h3 {
    color: var(--green)
}

.valueItem p {
    font-size: 14px;
    line-height: 20px;
    color: var(--light-grey);
    margin-left: 80px;
}